import React, { useEffect } from 'react';
import Head from 'next/head';
import App, { Container } from 'next/app';
import ErrorPage from 'next/error';
import config from '../config';

export const ERROR_PROP = '$$error';
let loaded;

export default class extends App {
  static async getInitialProps({ Component, ctx }) {
    if (!Component.getInitialProps) {
      return { pageProps: {} };
    }

    try {
      return { pageProps: await Component.getInitialProps(ctx) };
    } catch (err) {
      return {
        pageProps: {
          [ERROR_PROP]: { status: err.status || 500, message: err.message },
        },
      };
    }
  }

  render() {
    useEffect(() => {
      if (!loaded) {
        loaded = true;
        import('webfontloader')
          .then((WebFont) => {
            WebFont.load({
              typekit: {
                id: 'uaf4juj',
              },
            });
          })
          .catch((err) => console.error(err));
      }
    });
    const { Component, pageProps } = this.props;

    if (pageProps[ERROR_PROP]) {
      return (
        <ErrorPage statusCode={pageProps[ERROR_PROP].status}>
          {pageProps[ERROR_PROP].message}
        </ErrorPage>
      );
    }

    return (
      <Container>
        <Head>
          <title>{pageProps.title || config.title}</title>
          <meta
            name="viewport"
            content="initial-scale=1.0, width=device-width"
            key="viewport"
          />
        </Head>
        <Component {...pageProps} />
      </Container>
    );
  }
}
